import * as React from "react"
import Header from "../components/header"
import Footer from "../components/footer"
import style from "../styles.css"
import { useState } from 'react'
import isNarrowWindow from "../helpers/is_narrow_window"
import ContentSection from "../components/content_section"
import TextBody from "../components/text_body"
import TextImage from "../components/text_image"
import Group from "../components/group"
import TextList from "../components/text_list"
import { StaticImage } from "gatsby-plugin-image"
import { Link } from "gatsby"

const RESUME_LINK = "https://drive.google.com/file/d/1JNqQLoUmBqHr5Y3_U7rjMR-Bc_ptFxRe/view?usp=sharing"

const logoDivStyle = {
  display: "flex",
  flexDirection: "row",
  justifyItems: "center",
  gap: "20px"
}
const logoTextStyle = {
  fontFamily: "Roboto",
  fontSize: "16px",
  fontWeight: 400,
  color: "#000000",
}
const titleStyle = {
  fontFamily: "Poppins",
  fontSize: 14,
  fontWeight: 600,
  textTransform: "uppercase",
  letterSpacing: "0.24em",
  marginBottom: 0
}
const linkStyle = {
  textDecoration: "none"
}

function AboutPage(props) {
  const [narrowWindow, setNarrowWindow] = useState(isNarrowWindow)

  if (typeof window === `undefined`) return(<></>)
  window.addEventListener('resize', () => setNarrowWindow(isNarrowWindow()))

  return (
    <main style={style}>
      <title>ABOUT PAGE</title>
      <Header narrowWindow={narrowWindow}/>
      <ContentSection narrowWindow={narrowWindow} title={"(:"}>
        <TextImage
          title={(<p>Hello!!<br/><br/>My name is Ashley.</p>)} 
          imageName={"AB_me"} 
          alt={"Portrait of Ashley"} 
          narrowWindow={narrowWindow}
        />        
      </ContentSection>
      <ContentSection narrowWindow={narrowWindow} title="intro">
        <TextBody
          title={"About"}
          body={"I am a UI and graphic designer based in Vancouver, Canada. \
          My goal as a designer is to create simple yet meaningful designs and \
          solutions through my skills and interests in graphic design, photography, \
          product design, as well as UX/UI. Other than that, I really enjoy eating."}
          narrowWindow={narrowWindow}
        />
        <Group narrowWindow={narrowWindow}>
          <TextList title={"Role"} items={["UI design", "Graphic design"]}/>
          <TextList title={"Tools"} items={["Illustrator", "InDesign", "Photoshop", "Procreate", "Figma"]}/>
        </Group>
        <TextBody
          title={"Resume"}
          body={"Here's my resume!"}
          links={[{keyword:"resume!", address:RESUME_LINK}]}
          />

        <h1 style={titleStyle}>Let's connect</h1>
        <Group narrowWindow={narrowWindow}>
          <a href="https://www.instagram.com/akwstudios/" target="_blank" style={linkStyle}>
            <div style={logoDivStyle}>
              <StaticImage src="../images/instagram.png" alt="instagram" objectFit={"contain"}/>
              <p style={logoTextStyle}>@akwstudios</p>
            </div>
          </a>

          <a href="mailto:akwvisuals@gmail.com" target="_blank" style={linkStyle}>
            <div style={logoDivStyle}>
              <StaticImage src="../images/email.png" alt="instagram" objectFit={"contain"}/>
              <p style={logoTextStyle}>akwvisuals@gmail.com</p>
            </div>
          </a>

          <a href="https://www.linkedin.com/in/ashlewong" target="_blank" style={linkStyle}>
            <div style={logoDivStyle}>
              <StaticImage src="../images/linkedin.png" alt="instagram" objectFit={"contain"}/>
              <p style={logoTextStyle}>linkedin</p>
            </div>
          </a>
        </Group>
      </ContentSection>
      <Footer/>
    </main>
  )
}

export default AboutPage;